body {
  background: #000000;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mybanner{
  height: 30%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 60%;
  margin-top: 10px;

}
.onelogo{
  width: 250px;
  height: 90px;
  
  display: inline;
  margin-left: auto;
  margin-right: auto;
}

.twologo{
  width: 100px;
  height: 100px;
  display: inline;
  margin-left: 35%;
  margin-right: auto;
}

@media screen and (max-width: 600px) {

  .mybanner{
    height: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    margin-top: 10px;
  
  }
  .onelogo{
    width: 140px;
    height: 50px;
    
    display: inline;
    margin-left: auto;
    margin-right: auto;
  }
  
  .twologo{
    width: 60px;
    height: 60px;
    display: inline;
    margin-left: 15%;
    margin-right: auto;
  }

}